.App {
  text-align: center;
  font-family: 'Playfair Display', sans-serif;
}
header {
  background: -webkit-gradient(linear, left top, left bottom, from(#FFFFFF), color-stop(#C5DCE5), to(#FFFFFF));
  background: -webkit-linear-gradient(#FFFFFF, #C5DCE5, #FFFFFF);
  background: -o-linear-gradient(#FFFFFF, #C5DCE5, #FFFFFF);
  background: linear-gradient(#FFFFFF, #C5DCE5, #FFFFFF);
  height: 20vh;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-pack: start;
      justify-content: flex-start;
}
h1, h2, h3, h4 {
  font-family: 'Averia Serif Libre', serif;
  margin: 0;
  padding: 10px 0;
}
h1 {
  font-size: 60px;
}
h2 {
  font-size: 40px;
}
h3 {
  font-size: 25px;
}
h4 {
  font-size: 16px;
}
.linkBox {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-flow: row;
      flex-flow: row;
  /*justify-content: flex-start;*/
  width: 100vw;
  font-size: 15px;
  min-height: 25px;
}
.linkBox > a {
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
  padding: 0 20px;
}

.first {
  display: inline-block;
  text-decoration: none;
  border: none;
  background-color: white;
  color: black;
  -webkit-transition: all 0.25s ease-in;
  -o-transition: all 0.25s ease-in;
  transition: all 0.25s ease-in;
}
.second {
  display: inline-block;
  text-decoration: none;
  border: 1px solid lightgray;
  background-color: gray;
  color: white;
  -webkit-transition: all 0.25s ease-in;
  -o-transition: all 0.25s ease-in;
  transition: all 0.25s ease-in;
}
.third {
  display: inline-block;
  text-decoration: none;
  border: 1px solid gray;
  background-color: #333333;
  color: lightgray;
  -webkit-transition: all 0.25s ease-in;
  -o-transition: all 0.25s ease-in;
  transition: all 0.25s ease-in;
}
/* NOTE: STYLES FOR HOME PAGE */
.home-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
}
.home-container h3 {
  margin: 5px auto;
}
.div-img {
  width: 40vw;
  background-repeat: no-repeat;
  background-size: cover;
}

.fg-img {
  width: 40vw;
  opacity: 1;
  -moz-transition: visibility 1s ease-in-out;
  -webkit-transition: visibility 1s ease-in-out;
  -webkit-transition-property: opacity;
  -o-transition-property: opacity;
  transition-property: opacity;
  -webkit-transition-duration: 0.5s;
       -o-transition-duration: 0.5s;
          transition-duration: 0.5s;
  -webkit-transition-timing-function: ease-in-out;
       -o-transition-timing-function: ease-in-out;
          transition-timing-function: ease-in-out;
}
.fg-img:hover {
  opacity: 0;
}

ul {
  list-style: none;
}
/* ***************  BEGIN WIDE SCREEN STYLE ******************************** */
@media (min-width: 1024px) {
  h1 {
    font-size: 80px;
  }
  h2 {
    font-size: 60px;
  }
  h3 {
    font-size: 40px;
  }
  h4 {
    font-size: 20px;
  }
  .linkBox {
    font-size: 30px;
  }
  .div-img {
    width: 20vw;
    margin: auto;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .home-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    /*align-items: center;*/

  }
  .home-container > h2 {
    -ms-flex: 1 0 100%;
        flex: 1 0 100%;
  }
  .home-container div {
    text-align: center;
    -ms-flex: 0 1 30%;
        flex: 0 1 30%;
  }
  .fg-img {
    width: 20vw;
  }
  .footer-line {
    padding-top: 50px;
  }
}



